
import {Notify} from 'vant';
import {VanNotify} from 'vant/types/notify';

export enum NoteType {
    Error = 1,
    Info,
}

export interface Note {
    type: NoteType;
    message: string;
}


class Notes {

    public static New(): Notes {
        return new Notes();
    }

    public static NewNote(op: {type: NoteType, message: string}): Note {
        return op;
    }
    public list = new Array<Note>();
    public maxCount = 100;

    public add(note: Note): VanNotify {
        while (this.list.length >= this.maxCount) {
            this.list.pop();
        }
        this.list.push(note);

        let type = 'success';
        if (note.type === NoteType.Error) {
            type = 'primary';
        }
        let n: VanNotify;
        if ( note.type === NoteType.Error) {
            n = Notify({message: note.message, duration: 1000, type: 'warning'});
        } else {
            n = Notify({message: note.message, duration: 1000, type: 'primary'});
        }
        return n;
    }



}

export const notes = Notes.New();
