export abstract class ChainBase {
    public id = ''; // 是cert记录的唯一标识
    public txid = ''; // 上链时的交易id
    public address = ''; // 上链签名人的地址
    public ts = 0; // 上链的时间戳， 由链确定

    public status = StatusChain.None; //

    // 上链过程
    public process(s: StatusChain): void {
        this.status = s;
    }
}

// gia证书类型
export class CertGia extends ChainBase {
    public gia = ''; // 证书号
    public carat = ''; // 重量
    public color = ''; // 色泽
    public clarity = ''; // 透明度
    public cut = ''; // 切工标准

    public hash = ''; // 证书文件的hash256

    public toCert(): Cert {
        const cert = new Cert();
        cert.i18n = true; // 支持多语言，而其它类型的
        cert.certType = CertType.Gia;
        Object.assign(cert, this as ChainBase);

        cert.map.push({key: 'gia', value: this.gia});
        cert.map.push({key: 'carat', value: this.carat});
        cert.map.push({key: 'color', value: this.color});
        cert.map.push({key: 'clarity', value: this.clarity});
        cert.map.push({key: 'cut', value: this.cut});

        return cert;
    }

    public toGia(cert: Cert): boolean {
        // todo 检查所有的key， 如果与 Gia证书的完全一至，那么就为 gia证书， 如果证书是从链上读取下来，它是没有类型的
        if (cert.certType === CertType.Gia) {
            Object.assign(this, cert as ChainBase);
            this.gia = cert.map[0].value;
            this.carat = cert.map[1].value;
            this.color = cert.map[2].value;
            this.clarity = cert.map[3].value;
            this.cut = cert.map[4].value;
        }
        return false;
    }
}

// 所有证书
export class Cert extends ChainBase {
    public map = new Array<{key: string, value: string}>(); // map没有顺序，所以使用array
    public i18n = false; // 证书中的key是否支持多语言，如果支持在页面显示时，要进行翻译
    public certType = CertType.None;
    public getId(): string {
        if (!this.id) {
            let index = this.map.findIndex((it) => {
                return it.key === 'gia';
            });

            if ( index < 0 ) {
                index = this.map.findIndex((it) => {
                    return it.key === 'id';
                });
            }
            if (index >= 0) {
                this.id = this.map[index].value;
            }
        }
        return this.id;
    }
}

export class Own extends ChainBase {
    public str = ''; // 输入的任意字符，不能超过19个
}

export class CertOwn {
    public cert = new Cert();
    public own = new Own();
    public hasOwn(): boolean {
        if (this.own != null && this.own.txid != null) {
            return true;
        } else {
            return false;
        }
    }
}

export class Business extends ChainBase {
    public authAddress = ''; // 授权地址
    public name = ''; // 定义的名字，不超过12个字符
    public count = -1; // 授权次数, 只有为授权时才有效
    public auth = true; // 是否授权， false为取消授权

    public nonce = 0; // 每次自加1，这样来解决重复提交签名的问题
}

