import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';

// import 常用组件
import {Notify} from 'vant';



Vue.config.productionTip = false;
import { Langs, setLang, i18n, langInit } from '@/assets/i18n/lang';
import 'vant/lib/notify/style';
Vue.use(Notify);
langInit();
Vue.prototype.Local = (lang: Langs): void => {
  setLang(lang);
};

new Vue({
  router,
  store,
  i18n,
  render: (h) => h(App),
}).$mount('#app');
