

















































  import {Component, Vue} from 'vue-property-decorator';
  import {Cell, CellGroup, Col, Field, Icon, NavBar, Notify, Picker, Popup, Row} from 'vant';
  import {notes, NoteType} from '@/note';
  import {Langs, setLang} from '@/assets/i18n/lang';
  import 'vant/lib/popup/style';
  import 'vant/lib/icon/style';
  // import 'vant/lib/icon/local.css';
  import 'vant/lib/nav-bar/style';
  import 'vant/lib/cell/style';
  import 'vant/lib/cell-group/style';
  import 'vant/lib/field/style';
  import 'vant/lib/notify/style';
  import 'vant/lib/picker/style';

  import {Cert, CertOwn, Status} from '@/entry/diamond';

  Vue.config.productionTip = false;
Vue.use(Popup);
Vue.use(Icon);
Vue.use(NavBar);
Vue.use(Field);
Vue.use(Row).use(Col);
Vue.use(CellGroup).use(Cell);
Vue.use(Notify);
Vue.use(Picker);

@Component({
  components: {
  },
})
export default class Home extends Vue {

    private value = '';
    private showPicker = false;
    private columns = ['中文', 'ENGLISH', '日本語', '한국어'];

    private certNumber='';
    public certOwn = new CertOwn();
    public cert = new Cert();

    private onConfirm(value:string):void {
      this.value = value;
      if(value=='ENGLISH'){
        setLang(Langs.en)
      }
      if(value=='中文'){
        setLang(Langs.zh)
      }
      if(value=='한국어'){
        setLang(Langs.ko)
      }
      if(value=='日本語'){
        setLang(Langs.ja)
      }
      this.showPicker = false;
    }

    public onClickRight(): void {
        this.$store.state.mainMenu.show = true;
    }

  public onClickSearch(): void {
    if (!this.certNumber) {
      notes.add({type: NoteType.Error, message:this.$t('notifyMsg.nullCertNum').toString()});
    } else {
      this.$store.state.data.diamond.findCertByGia(this.certNumber).then((cert: Cert | Status) => {
        // todo check

        if (cert as Cert) {
          this.cert = cert as Cert;
          this.$store.state.data.cert = this.cert;
          this.$router.push('/frame/cert/show');
        } else {
          window.console.log(cert);

          notes.add({type: NoteType.Error, message: this.$t('notifyMsg.errorCertNum').toString()});

        }
      });
    }
  }

    public onClickMenu(path: string): void {
        this.$router.push(path);
    }
    public onClickScan(): void {
      // todo 扫描二维码

      // todo 判断为拥有上链扫码签名
      // this.$router.push({naem:'own_scan_signed',params:{}});

      //todo 判断为业务签名
      this.$router.push({name:'business_sign',params:{}});
      // todo 判断为original
      // this.$router.push({name: 'original_sign', params: {}})
    }
}
