import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/search.vue';

Vue.use(Router);

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/', name: 'cert_search',  component: Home, alias: '/search',
    },
    {
      path: '/frame',   name: 'frame',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import('./views/frame.vue'),

      children: [
        // certOwn
        {
          path: 'cert/confirm', name: 'cert_confirm', component: () => import('./views/cert/confirm.vue'),
        },
        {
          path: 'cert/gia_frame', name: 'cert_gia_frame', props: true, component: () => import('./views/cert/gia_frame.vue'),
        },
        {
          path: 'cert/input_cert', name: 'cert_input_cert', component: () => import('./views/cert/input_cert.vue'),
        },
        {
          path: 'cert/list', name: 'cert_list', component: () => import('./views/cert/list.vue'),
        },
        {
          path: 'cert/prepare', name: 'cert_prepare', component: () => import('./views/cert/prepare.vue'),
        },
        {
          path: 'cert/process', name: 'cert_process', component: () => import('./views/cert/process.vue'),
        },
        {
          path: 'cert/show', name: 'cert_show', component: () => import('./views/cert/show.vue'),
        },
        // business
        {
          path: 'business/input', name: 'business_input', component: () => import('./views/business/input.vue'),
        },
        {
          path: 'business/list', name: 'business_list', component: () => import('./views/business/list.vue'),
        },
        {
          path: 'business/scan_sign', name: 'business_sign', component: () => import('./views/business/scan_sign.vue'),
        },
        {
          path: 'business/process', name: 'business_process', component: () => import('./views/business/process.vue'),
        },

        // original
        {
          path: 'original/apply', name: 'original_apply', component: () => import('./views/original/apply.vue'),
        },
        {
          path: 'original/input', name: 'original_input', component: () => import('./views/original/input.vue'),
        },
        {
          path: 'original/sign', name: 'original_sign', component: () => import('./views/original/scan_sign.vue'),
        },
        {
          path: 'original/process', name: 'original_process', component: () => import('./views/original/process.vue'),
        },

        // own
        {
          path: 'own/input', name: 'own_input', component: () => import('./views/own/input.vue'),
        },
        {
          path:'own/confirm',name:'own_confirm',component:()=>import('./views/own/confirm.vue'),
        },
        {
          path:'own/process',name:'own_process',component:()=>import('./views/own/process.vue'),
        },
        {
          path:'own/scanSigned',name:'own_scan_signed',component:()=>import('./views/own/scanSigned.vue'),
        },

      ],
    },
    {
      path: '/scaned', name: 'scaned', component: () => import('./views/scan/scaned.vue'), props: true,
    },

  ],
});
